	module.exports = "<section><h2>Layout 栅格</h2> <p>24 栅格系统。</p> <h2>设计理念</h2> <div class=layout-demo> <div class=\"ant-row demo-row\"> <div class=\"ant-col-24 demo-col demo-col-1\"> 100% </div> </div> <div class=\"ant-row demo-row\"> <div class=\"ant-col-6 demo-col demo-col-2\"> 25% </div> <div class=\"ant-col-6 demo-col demo-col-3\"> 25% </div> <div class=\"ant-col-6 demo-col demo-col-2\"> 25% </div> <div class=\"ant-col-6 demo-col demo-col-3\"> 25% </div> </div> <div class=\"ant-row demo-row\"> <div class=\"ant-col-8 demo-col demo-col-4\"> 33.33% </div> <div class=\"ant-col-8 demo-col demo-col-5\"> 33.33% </div> <div class=\"ant-col-8 demo-col demo-col-4\"> 33.33% </div> </div> <div class=\"ant-row demo-row\"> <div class=\"ant-col-12 demo-col demo-col-1\"> 50% </div> <div class=\"ant-col-12 demo-col demo-col-3\"> 50% </div> </div> <div class=\"ant-row demo-row\"> <div class=\"ant-col-16 demo-col demo-col-4\"> 66.66% </div> <div class=\"ant-col-8 demo-col demo-col-5\"> 33.33% </div> </div> </div> <p>在多数业务情况下，Ant Design需要在设计区域内解决大量信息收纳的问题，因此在 12 栅格系统的基础上，我们将整个设计建议区域按照 24 等分的原则进行划分。</p> <p>划分之后的信息区块我们称之为『盒子』。建议横向排列的盒子数量最多四个，最少一个。『盒子』在整个屏幕上占比见上图。设计部分基于盒子的单位定制盒子内部的排版规则，以保证视觉层面的舒适感。</p> <h2>概述</h2> <p>布局的栅格化系统，我们是基于行（row）和列（col）来定义信息区块的外部框架，以保证页面的每个区域能够稳健地排布起来。下面简单介绍一下它的工作原理：</p> <ul> <li>通过<code>row</code>在水平方向建立一组<code>column</code>（简写col）</li> <li>你的内容应当放置于<code>col</code>内，并且，只有<code>col</code>可以作为<code>row</code>的直接元素</li> <li>栅格系统中的列是指1到24的值来表示其跨越的范围。例如，三个等宽的列可以使用<code>.col-8</code>来创建</li> <li>如果一个<code>row</code>中的<code>col</code>总和超过 24，那么多余的<code>col</code>会作为一个整体另起一行排列</li> </ul> <h2>Flex 布局</h2> <p>我们的栅格化系统支持 Flex 布局，允许子元素在父节点内的水平对齐方式 - 居左、居中、居右、等宽排列、分散排列。子元素与子元素之间，支持顶部对齐、垂直居中对齐、底部对齐的方式。同时，支持使用 order 来定义元素的排列顺序。</p> <p>Flex 布局是基于 24 栅格来定义每一个『盒子』的宽度，但排版则不拘泥于栅格。</p> <h2>代码演示</h2> <p><demo></demo></p> <h2>API</h2> <h3>Row</h3> <table> <thead> <tr> <th>成员</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>gutter</td> <td>栅格间隔</td> <td>number</td> <td>0</td> </tr> <tr> <td>type</td> <td>布局模式，可选 <code>flex</code>，现代浏览器下有效</td> <td>string</td> <td></td> </tr> <tr> <td>align</td> <td>flex 布局下的垂直对齐方式：<code>top</code> <code>middle</code> <code>bottom</code></td> <td>string</td> <td><code>top</code></td> </tr> <tr> <td>justify</td> <td>flex 布局下的水平排列方式：<code>start</code> <code>end</code> <code>center</code> <code>space-around</code> <code>space-between</code></td> <td>string</td> <td><code>start</code></td> </tr> </tbody> </table> <h3>Col</h3> <table> <thead> <tr> <th>成员</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>span</td> <td>栅格占位格数，为 0 时相当于 <code>display: none</code></td> <td>number</td> <td>-</td> </tr> <tr> <td>order</td> <td>栅格顺序，<code>flex</code> 布局模式下有效</td> <td>number</td> <td>0</td> </tr> <tr> <td>offset</td> <td>栅格左侧的间隔格数，间隔内不可以有栅格</td> <td>number</td> <td>0</td> </tr> <tr> <td>push</td> <td>栅格向右移动格数</td> <td>number</td> <td>0</td> </tr> <tr> <td>pull</td> <td>栅格向左移动格数</td> <td>number</td> <td>0</td> </tr> <tr> <td>xs</td> <td><code>&lt;768px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td> <td>number or object</td> <td>-</td> </tr> <tr> <td>sm</td> <td><code>≥768px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td> <td>number or object</td> <td>-</td> </tr> <tr> <td>md</td> <td><code>≥992px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td> <td>number or object</td> <td>-</td> </tr> <tr> <td>lg</td> <td><code>≥1200px</code> 响应式栅格，可为栅格数或一个包含其他属性的对象</td> <td>number or object</td> <td>-</td> </tr> </tbody> </table> </section>";

