	module.exports = "<section _v-7a07f13e=\"\"><h2 _v-7a07f13e=\"\">Icon 图标</h2> <p _v-7a07f13e=\"\">有含义的矢量图形，每一个图标打倒一个敌人。</p> <h2 _v-7a07f13e=\"\">图标的命名规范</h2> <p _v-7a07f13e=\"\">我们为每个图标赋予了语义化的命名，命名规则如下:</p> <ul _v-7a07f13e=\"\"> <li _v-7a07f13e=\"\"> <p _v-7a07f13e=\"\">实心和描线图标保持同名，用 <code _v-7a07f13e=\"\">-o</code> 来区分，比如 <code _v-7a07f13e=\"\">question-circle</code>(实心) 和 <code _v-7a07f13e=\"\">question-circle-o</code>(描线)；</p> </li> <li _v-7a07f13e=\"\"> <p _v-7a07f13e=\"\">命名顺序：<code _v-7a07f13e=\"\">[icon名]-[形状可选]-[描线与否]-[方向可选]</code>。</p> </li> </ul> <h2 _v-7a07f13e=\"\">如何使用</h2> <p _v-7a07f13e=\"\">使用 <code _v-7a07f13e=\"\">&lt;ant-icon&gt;&lt;/ant-icon&gt;</code> 标签声明组件，指定图标对应的 type 属性，示例代码如下:</p> <pre _v-7a07f13e=\"\"><code class=language-html _v-7a07f13e=\"\"><span class=hljs-tag _v-7a07f13e=\"\">&lt;<span class=hljs-name _v-7a07f13e=\"\">ant-icon</span> <span class=hljs-attr _v-7a07f13e=\"\">type</span>=<span class=hljs-string _v-7a07f13e=\"\">\"link\"</span>&gt;</span><span class=hljs-tag _v-7a07f13e=\"\">&lt;/<span class=hljs-name _v-7a07f13e=\"\">ant-icon</span>&gt;</span>\n</code></pre> <p _v-7a07f13e=\"\">最终会渲染为：</p> <pre _v-7a07f13e=\"\"><code class=language-html _v-7a07f13e=\"\"><span class=hljs-tag _v-7a07f13e=\"\">&lt;<span class=hljs-name _v-7a07f13e=\"\">i</span> <span class=hljs-attr _v-7a07f13e=\"\">class</span>=<span class=hljs-string _v-7a07f13e=\"\">\"anticon anticon-${type}\"</span>&gt;</span><span class=hljs-tag _v-7a07f13e=\"\">&lt;/<span class=hljs-name _v-7a07f13e=\"\">i</span>&gt;</span>\n</code></pre> <h2 _v-7a07f13e=\"\">本地部署</h2> <p _v-7a07f13e=\"\">图标组件使用 <a href=http://iconfont.cn _v-7a07f13e=\"\">iconfont.cn</a>，默认公网可访问。如需本地部署，可参考 <a href=https://github.com/ant-design/antd-init/tree/master/examples/local-iconfont _v-7a07f13e=\"\">示例</a>。</p> <h2 _v-7a07f13e=\"\">图标列表</h2> <blockquote _v-7a07f13e=\"\"> <p _v-7a07f13e=\"\">点击图标复制代码。</p> </blockquote> <h3 _v-7a07f13e=\"\">一. 方向性图标</h3> <ul class=anticons-list _v-7a07f13e=\"\"> <copyable-icon v-for=\"icon in icons\" :type=icon _v-7a07f13e=\"\"></copyable-icon> </ul> <h3 _v-7a07f13e=\"\">二. 提示建议性图标</h3> <ul class=anticons-list _v-7a07f13e=\"\"> <copyable-icon v-for=\"icon in icons2\" :type=icon _v-7a07f13e=\"\"></copyable-icon> </ul> <h3 _v-7a07f13e=\"\">三. 网站通用图标</h3> <ul class=anticons-list _v-7a07f13e=\"\"> <copyable-icon v-for=\"icon in icons3\" :type=icon _v-7a07f13e=\"\"></copyable-icon> </ul> </section>";

